.md blockquote {
  font-family: var(--font-baskerville);
  // background-color: rgba(148, 148, 149, 0.08) ;
  font-size: 1.5em;
  line-height: 100%;
  margin: 1em 0px;
  padding: 1.1em 20px 1px 20px;
  // padding: 0 2em;
  // border-left: 8px solid var(--pm);
  font-weight: 900;
  position: relative;
}

blockquote:before, blockquote:after{
  position: absolute;
  font-size: 3rem;
}

.md blockquote:before {
  content: "\201C";
  left: -0.2em;
  top: 1em;
}
