.md table {
  border-collapse: collapse;
  border-spacing: 0;
  display: block;
  max-width: 100%;
  width: 100%;
  margin: 1em 0;
  overflow: auto;
}

.md table th {
  background-color: var(--bg);
}

.md table th, table td {
  padding: 10px 20px;
  border: 1px solid var(--bd);
}

.md img {
  width: 100%;
  max-width: 100%;
}

.md li {
  margin-top: .5em;
}