@use '../util' as *;

.container {
    width: 90%;
    margin: 0 auto ;
    @include breakpoint(large) {
      width: 48%;
    }
  }
  
  .wrapper-masthead {
    background-color: var(--clr-dark);
    color: var(--clr-light);
    width:100%;
  }
  
  .inner,
  .content {
    width: 100%;
    margin: 0 auto;
    padding: 0.7em;
     @include breakpoint(medium) {
       width: 55%;
     }
  }

  p, dt, dl{
    max-width: 50ch;
  }
  