// General reset of margins

*::after,
::before {
  margin-top: 0;
}

// Owl reset of top margins

* + * {
  margin-top: 0;
  margin-bottom: 0em;
}

p + p, li + li {
  margin-top: 1em;
}

// General headings

h1,h2,h3,label{
	line-height:1em;
}

h1{
	font-size:5rem;
}

h2,h3{
  margin-top: 1.5em;
	margin-bottom:0.5em
}
h3{
	font-size:1.5rem
}

a {
  overflow-wrap: break-word;
  word-wrap: break-word;
}

a{
	text-decoration:none;
	color:var(--clr-link);
	overflow-wrap:break-word;
	word-wrap:break-word
}
a:visited{
	color:var(--clr-link_visited);
}
a:hover{
	text-decoration:underline
}

h2 a{
	color:var(--clr-darkest);
	text-decoration: underline
}


// Masthead Brand

h1 {
  font-size: 2.7rem;
}

h1 > a {
  // text-decoration: none;
  color: var(--clr-darkest);
}

h1 > a:visited {
  color: var(--clr-darkest);
}

h1 > a:active {
  // text-decoration: none;
  color: var(--clr-darkest);
}
h1 > a:hover {
  // text-decoration: underline;
  color: var(--clr-darkest);
}