@use '../util' as *;

html {
  font-size: 1em;
  box-sizing: border-box;
  @include breakpoint(medium) {
    font-size: 1.2em;
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0 0 0 0;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  font-family: var(--font-baskerville);
  color: var(--clr-darkest);
}

