// Using css variables

:root {
  --clr-dark: hsl(241, 86%, 17%);
  --clr-light: hsl(240, 81%, 96%);
  --clr-secondary: hsl(260, 3, 34);
  --clr-warning: hsl(15, 94, 54);
  --clr-danger: hsl(9, 100, 43);
  --clr-primary: rgb(5, 223, 23);
  --clr-darkest: hsl(240, 82%, 4%);
  --clr-link: rgb(4, 7, 202);
  --clr-link_visited: rgb(104, 0, 189);
}
